body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    font-family: 'Baloo Thambi', cursive;
}

.navbar {
    background: url(/react-guess-the-flag-game/static/media/earth.1f013425.jpg) no-repeat;
    background-size: cover;
}

.answer {
    font-size: 40px;
    position: absolute;
    top:0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    height: 30%;
    width: 400px;
    background: rgba(255, 255, 255, 0.769);
    border-radius: 10px;
    border: 4px solid rgba(128, 128, 128, 0.598);
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-name: answer-animation;
            animation-name: answer-animation;
}

.correct-country {
    font-size: 30px;
    text-align: center;
}
  
  @-webkit-keyframes answer-animation {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }

    25% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
  
    50% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    75% {
      -webkit-transform: scale(1.5);
              transform: scale(1.5);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
  }
  
  @keyframes answer-animation {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }

    25% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
  
    50% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    75% {
      -webkit-transform: scale(1.5);
              transform: scale(1.5);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
  }

@media (max-width: 1000px) {
    .form-check {
        display: flex;
    }

    .display-2 {
        font-size: 2rem;
    }

    .answer {
        font-size: 70px;
    }
}
@media (max-width: 700px) {
    .answer {
        font-size: 40px;
    }
    .correct-country {
        font-size: 30px;
    }
}
